import { useEffect, useState } from 'react';
import { AppRoot, Button } from '@telegram-apps/telegram-ui';
import { getTonConnect, ensureWalletConnected, disconnectWallet } from './tonConnectInstance';
import { ProfileRowContainer } from './components/ProfileRowContainer';
import { ProfileRow } from './components/ProfileRow';
import { getJettonsFromTonAPI } from './components/getJettonsFromTonAPI';
import { assets } from './data/assets';
import { Address } from '@ton/core';
import md5 from 'md5';

type MyUser = {
  first_name: string;
  last_name?: string;
  username?: string;
  photo_url?: string;
};

const defaultUsername = 'johndoe';
const defaultFirstName = 'John';
const defaultLastName = 'Doe';

function normalizeAddress(id: string): string {
  try {
    return Address.parseFriendly(id).address.toRawString();
  } catch {
    return '';
  }
}

export const Profile = () => {
  const rawUser = typeof window !== 'undefined' && window.Telegram?.WebApp?.initDataUnsafe?.user;
  const user = (rawUser as unknown as MyUser) || null;
  const username = user?.username;
  const photoUrl = user?.photo_url;

  const tonConnect = getTonConnect();
  const [account, setAccount] = useState(tonConnect.account);
  const [totalAssets, setTotalAssets] = useState<number>(0);
  const [usdtBalance, setUsdtBalance] = useState<number>(0);

  const tokenSymbols = ['USDT', 'SANDS5', 'SANDS6'];

  useEffect(() => {
    const unsubscribe = tonConnect.onStatusChange((wallet) => {
      setAccount(wallet?.account || null);
    });
    return () => unsubscribe();
  }, [tonConnect]);

  useEffect(() => {
    const fetchBalances = async () => {
      if (!account?.address) {
        setTotalAssets(0);
        setUsdtBalance(0);
        return;
      }

      try {
        const jettons = await getJettonsFromTonAPI(account.address, tokenSymbols);

        //    USDT
        const usdt = jettons.find((j) => j.symbol === 'USDT');
        const usdtAmount = usdt ? Number(usdt.balance) : 0;
        setUsdtBalance(usdtAmount);

        //      (symbol+address )
        const validJettons = jettons.filter((j) => {
          if (!j.symbol || !j.master) return false;
          return assets.some((asset) =>
            asset.symbol === j.symbol &&
            normalizeAddress(asset.id) === j.master
          );
        });

        const totalInvested = validJettons.reduce((acc, j) => acc + Number(j.balance), 0);
        setTotalAssets(totalInvested);

      } catch (e) {
        console.error('   Jetton :', e);
        setTotalAssets(0);
        setUsdtBalance(0);
      }
    };

    fetchBalances();
  }, [account]);

  const handleConnect = async () => {
     try {
       await ensureWalletConnected();
     } catch (err: any) {
       //      
       if (err.name !== 'WalletAlreadyConnectedError') {
         console.error('Connect error:', err);
         alert(` : ${err.message || err}`);
       }
     }
   };
  const handleDisconnect = () => disconnectWallet();

  const firstName = user?.first_name || defaultFirstName;
  const lastName = user?.last_name || defaultLastName;
  const nameForHash = `${firstName}${lastName}`.trim().toLowerCase();
  const avatarHash = md5(nameForHash);
  const avatarUrl = `https://www.gravatar.com/avatar/${avatarHash}?d=identicon`;

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
          padding: 16,
          paddingBottom: 50,
        }}
      >
        <div style={{ width: '100%', maxWidth: 530 }}>
          <div style={{ display: 'flex', alignItems: 'center', marginBottom: 16 }}>
            <img
              src={photoUrl || avatarUrl}
              alt="Avatar"
              style={{ width: 200, height: 200, borderRadius: '50%' }}
            />
            <div style={{ marginLeft: 12, lineHeight: 1.2 }}>
              <div style={{ fontSize: 30, color: 'var(--tg-theme-text-color, #000)' }}>
                @{username || defaultUsername}
              </div>
              <div style={{ fontSize: 30, color: 'var(--tg-theme-text-color, #000)' }}>
                {firstName + ' ' + lastName}
              </div>
            </div>
          </div>

          {account?.address && (
            <ProfileRowContainer>
              <ProfileRow label=":" value={account.address} copyable />
              <ProfileRow
                label=" :"
                value={`${totalAssets.toFixed(0)} USDT`}
              />
              <ProfileRow
                label=" USDT:"
                value={`${usdtBalance.toFixed(0)} USDT`}
                last
              />
            </ProfileRowContainer>
          )}

          <Button
            size="l"
            style={{ width: '100%', maxWidth: 510, marginBottom: 16 }}
            onClick={account ? handleDisconnect : handleConnect}
          >
            {account ? ' ' : '  TON'}
          </Button>
        </div>
      </div>
    </AppRoot>
  );
};
