// src/helpers/rawOnChain.ts

import { tonClient } from '../tonConnectInstance';
import { Address, TupleItemSlice } from '@ton/core';

const CACHE_KEY = 'raisedCache';
let cache: Record<string, number> = (() => {
  try {
    return JSON.parse(localStorage.getItem(CACHE_KEY) || '{}');
  } catch {
    return {};
  }
})();

/**
 *      id,  null  .
 */
export function getCachedRaised(id: string): number | null {
  return cache[id] ?? null;
}

/**
 *  fresh-  ,      .
 */
export async function fetchFullData(id: string): Promise<number> {
  console.log('rawOnChain  fetchFullData start, id=', id);
  try {
    const addr = Address.isFriendly(id)
      ? Address.parseFriendly(id).address
      : Address.parse(id);
    console.log('rawOnChain  parsed address=', addr.toString());

    const res = await tonClient.callGetMethod(
      addr,
      'getFullData',
      [] as TupleItemSlice[]
    );
    const rawItems = (res.stack as any).items as any[];
    console.log('rawOnChain  rawItems.length =', rawItems.length);

    if (rawItems.length < 7) {
      throw new Error(`Expected 7 tuple items, got ${rawItems.length}`);
    }

    // 7-   total_invested  -
    const elem6 = rawItems[6];
    if (elem6.type !== 'int') {
      throw new Error(`Unexpected type for total_invested: ${elem6.type}`);
    }
    const totalInvestedMicro = BigInt(elem6.value) as bigint;
    console.log('rawOnChain  totalInvestedMicro=', totalInvestedMicro);

    const result = Number(totalInvestedMicro);
    console.log('rawOnChain  fetchFullData result=', result);

    //       localStorage
    cache[id] = result;
    try {
      localStorage.setItem(CACHE_KEY, JSON.stringify(cache));
    } catch {}
    return result;
  } catch (err) {
    console.error('rawOnChain  fetchFullData error:', err);
    throw err;
  }
}
