import { useEffect, useState } from 'react';
import { AppRoot, Button } from '@telegram-apps/telegram-ui';
import { getTonConnect, tonClient } from './tonConnectInstance';
import { ProfileRowContainer } from './components/ProfileRowContainer';
import { ProfileRow } from './components/ProfileRow';
import { getJettonsFromTonAPI } from './components/getJettonsFromTonAPI';
import { useNavigate } from 'react-router-dom';
import { assets } from './data/assets';
import { Address, beginCell } from '@ton/core';
import type { TupleItemSlice } from '@ton/core';
import { AssetCard } from './components/AssetCard';

function normalizeAddress(id: string): string {
  try {
    return Address.parseFriendly(id).address.toRawString();
  } catch {
    return '';
  }
}

export const Projects = () => {
  const tonConnect = getTonConnect();
  const [account, setAccount] = useState(tonConnect.account);
  const [totalAssets, setTotalAssets] = useState(0);
  const [totalUSDT, setTotalUSDT] = useState(0);
  const [validAssets, setValidAssets] = useState<typeof assets>([]);
  const [pendingSum, setPendingSum] = useState(0);  // in USDT units

  const tokenSymbols = ['SANDS5', 'SANDS6'];

  useEffect(() => {
    const unsubscribe = tonConnect.onStatusChange(wallet => {
      setAccount(wallet?.account || null);
    });
    return () => unsubscribe();
  }, [tonConnect]);

  useEffect(() => {
    const fetchData = async () => {
      if (!account?.address) {
        setTotalAssets(0);
        setTotalUSDT(0);
        setValidAssets([]);
        setPendingSum(0);
        return;
      }

      // 1) Fetch Jettons & filter portfolio assets
      let matched = [] as typeof assets;
      try {
        console.log('[DEBUG] Fetching jettons for address:', account.address);
        const jettons = await getJettonsFromTonAPI(account.address, tokenSymbols);
        console.log('[DEBUG] Jettons:', jettons);

        const validJettons = jettons.filter((j: any) =>
          j.symbol && j.master &&
          assets.some(a =>
            a.symbol === j.symbol &&
            normalizeAddress(a.id) === j.master
          )
        );

        matched = assets.filter(a =>
          jettons.some((j: any) =>
            j.symbol === a.symbol &&
            normalizeAddress(a.id) === j.master
          )
        );

        const investedSum = validJettons.reduce((sum, j) => sum + Number(j.balance), 0);
        console.log('[DEBUG] Invested sum:', investedSum);

        setTotalAssets(investedSum);
        setTotalUSDT(validJettons.length);
        setValidAssets(matched);
      } catch (err) {
        console.error('   jettons:', err);
        setTotalAssets(0);
        setTotalUSDT(0);
        setValidAssets([]);
        setPendingSum(0);
        return;
      }

      // 2) Fetch pending withdrawals via getInvestorPendingJettons
      try {
        const pendings = await Promise.all(
          matched.map(async asset => {
            if (!asset.treasuryAddress) return 0;
            const treasuryAddr = typeof asset.treasuryAddress === 'string'
              ? Address.parseFriendly(asset.treasuryAddress).address
              : asset.treasuryAddress;

            console.log('[DEBUG] getInvestorPendingJettons for:', asset.id);

            const argCell = beginCell()
              .storeAddress(Address.parse(account.address))
              .endCell();
            console.log('[DEBUG] argCell hex:', argCell.toBoc().toString('hex'));

            const arg: TupleItemSlice = { type: 'slice', cell: argCell };

            const result = await tonClient.callGetMethod(
              treasuryAddr,
              'getInvestorPendingJettons',
              [arg]
            );
            console.log('[DEBUG] raw stack:', result.stack);

            // raw value is in micro-units (6 decimals)
            const rawPending = result.stack.readNumber();
            const pending = rawPending / 1_000_000;
            console.log('[DEBUG] rawPending:', rawPending, '=> pending USDT:', pending);
            return pending;
          })
        );

        const totalPending = pendings.reduce((a, b) => a + b, 0);
        console.log('[DEBUG] totalPendingSum:', totalPending);
        setPendingSum(totalPending);
      } catch (err) {
        console.error('   pendingSum:', err);
      }
    };

    fetchData();
  }, [account]);

  const navigate = useNavigate();

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, rgb(255, 255, 255))',
          padding: 16,
          paddingBottom: 100,
        }}
      >
        <div style={{ width: '100%', maxWidth: 530 }}>
          {account?.address ? (
            <>
              <ProfileRowContainer>
                <ProfileRow
                  label=" :"
                  value={`${totalAssets.toFixed(0)} USDT`}
                />
                <ProfileRow
                  label="  :"
                  value={`${totalUSDT}`}
                />
                <ProfileRow
                  label="  :"
                  value={`${pendingSum.toFixed(6).replace(/\.?0+$/,'')} USDT`}
                  last
                />
              </ProfileRowContainer>
              {validAssets.map(asset => (
                <AssetCard key={asset.id} asset={asset} />
              ))}
            </>
          ) : (
            <div>
              <div style={{
                fontSize: 20,
                color: 'var(--tg-theme-text-color, #000)',
                textAlign: 'center',
                marginBottom: 16,
              }}>
                      ,      
              </div>
              <Button
                size="l"
                style={{ width: '100%', maxWidth: 510, marginBottom: 16 }}
                onClick={() => navigate('/profile')}
              >
                  
              </Button>
            </div>
          )}
        </div>
      </div>
    </AppRoot>
  );
};
