import { Card, Text, Divider } from '@telegram-apps/telegram-ui';
import { useNavigate } from 'react-router-dom';
import { fetchFullData, getCachedRaised } from '../helpers/rawOnChain';
import { useEffect, useState } from 'react';

interface AssetCardProps {
  asset: {
    id: string;
    name: string;
    image: string;
    minInvest: string;
    returnRate: string;
    raised: number;
    goal: number;
  };
}

export const AssetCard: React.FC<AssetCardProps> = ({ asset }) => {
  const navigate = useNavigate();

  const cachedRaised = asset ? getCachedRaised(asset.id) : null;
  const [raised, setRaised] = useState<number>(cachedRaised ?? (asset?.raised ?? 0));

  const rawProgress = raised / asset.goal;
  const progress = raised > 0 ? Math.max(1, Math.floor(rawProgress * 100)) : 0;

  useEffect(() => {
    if (!asset) return;
    fetchFullData(asset.id)
      .then(val => {
        console.log('AssetBuy  fetched fresh raised=', val);
        setRaised(val);
      })
      .catch(err => console.error('AssetBuy  fetchFullData error:', err));
  }, [asset]);

  return (
    <Card
      key={asset.id}
      onClick={() => {
        window.scrollTo(0, 0);
        navigate(`/asset/${asset.id}`);
      }}
      style={{
        marginTop: 16,
        padding: 0,
        overflow: 'hidden',
        maxWidth: 530,
        position: 'relative',
        borderRadius: 16,
        backgroundColor: 'var(--tg-theme-bg-color, rgb(237, 239, 242))',
        cursor: 'pointer',
      }}
    >
      <img
        src={asset.image}
        alt={asset.name}
        style={{ width: '100%', height: '70%', maxWidth: 530, objectFit: 'cover' }}
      />
      <div style={{ padding: '16px' }}>
        <Text style={{ color: 'var(--tg-theme-text-color, #000)', fontWeight: 600, fontSize: 18 }}>
          {asset.name}
        </Text>

        <Divider style={{ margin: '-1px' }} />

        <Text style={{ color: 'var(--tg-theme-hint-color, #666)', fontSize: 14 }}>
           {asset.minInvest.toLocaleString()} USDT   {asset.returnRate}
        </Text>

        <Divider style={{ margin: '0px 0' }} />

        <div style={{
          height: 7,
          width: '100%',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, rgb(255, 255, 255))',
          borderRadius: 8,
          overflow: 'hidden',
          marginTop: 8,
        }}>
          <div style={{
            height: '100%',
            width: `${progress}%`,
            backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
          }} />
        </div>

        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          fontSize: 14,
          marginTop: 4,
          color: 'var(--tg-theme-text-color, #000)',
        }}>
          <span>{raised.toLocaleString()} USDT ({progress}%)</span>
          <span>{asset.goal.toLocaleString()} USDT</span>
        </div>
      </div>
    </Card>
  );
};
