import { useParams, useNavigate } from 'react-router-dom';
import { AppRoot, Title, Text, Button } from '@telegram-apps/telegram-ui';
import { Address, beginCell, toNano } from '@ton/core';
import { fetchFullData, getCachedRaised } from './helpers/rawOnChain';
import {
  ensureWalletConnected,
  getConnectedWallet,
  getTonConnect,
} from './tonConnectInstance';
import { assets } from './data/assets';
import { ProfileRow } from './components/ProfileRow';
import { ProfileRowContainer } from './components/ProfileRowContainer';
import { useEffect, useState } from 'react';

const SHOW_ADMIN = false;

export const AssetDetail = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const asset = assets.find((a) => a.id === id);
  const cachedRaised = asset ? getCachedRaised(asset.id) : null;
  const [raised, setRaised] = useState<number>(cachedRaised ?? (asset?.raised ?? 0));

  useEffect(() => {
    if (!asset) return;
    fetchFullData(asset.id)
      .then(val => {
        console.log('AssetBuy  fetched fresh raised=', val);
        setRaised(val);
      })
      .catch(err => console.error('AssetBuy  fetchFullData error:', err));
  }, [asset]);

  if (!asset) {
    return (
      <AppRoot>
        <div style={{ padding: 16 }}>
          <Text>  </Text>
          <Button onClick={() => navigate(-1)}> </Button>
        </div>
      </AppRoot>
    );
  }

  const rawProgress = raised / asset.goal;
  const progress = raised > 0 ? Math.max(1, Math.floor(rawProgress * 100)) : 0;

  const handleClaim = async () => {
    try {
      //   ,    
      await ensureWalletConnected();
      // ,   
      const wallet = getConnectedWallet();
      console.log('Connected wallet:', wallet.address);

      if (!asset.treasuryAddress) {
        console.error('Treasury address is not defined');
        return;
      }

      const treasuryAddr = typeof asset.treasuryAddress === 'string' 
          ? Address.parse(asset.treasuryAddress)
          : asset.treasuryAddress;

      const messageCell = beginCell()
        .storeUint(0x1234567c, 32) // op-code  ClaimJetton
        .endCell();

      const boc = messageCell.toBoc();
      const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
        .replace(/\+/g, '-')
        .replace(/\//g, '_')
        .replace(/=+$/, '');

      const tonConnect = getTonConnect();
      await tonConnect.sendTransaction({
        validUntil: Math.floor(Date.now() / 1000) + 600,
        messages: [
          {
            address: treasuryAddr.toString(),
            amount: toNano('0.5').toString(),
            payload: base64url,
          },
        ],
      });
      console.log('ClaimJetton transaction sent successfully');
      alert('  !');
    } catch (error: any) {
      console.error('Error in handleClaim:', error);
      alert(`: ${error.message ?? error}`);
    }
  };

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
          minHeight: '100vh',
        }}
      >
        <div style={{padding: 16, width: '100%', maxWidth: 530, paddingBottom: 100 }}>
          {/*  */}
          <Button
            onClick={() => navigate(-1)}
            size="l"
            style={{
              marginBottom: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #eee)',
              color: 'var(--tg-theme-text-color, #000)',
            }}
          >
             
          </Button>

          {/*  */}
          <img
            src={asset.image}
            alt={asset.name}
            style={{
              width: '100%',
              borderRadius: 16,
              objectFit: 'cover',
              marginBottom: 16,
            }}
          />

          {/*  +  */}
          <Title level="2" style={{ marginBottom: 4, color: 'var(--tg-theme-text-color, #000)' }}>
            {asset.name}
          </Title>

          <Text style={{ color: 'var(--tg-theme-hint-color, #666)', marginBottom: 12 }}>
            {asset.subtitle}
          </Text>

          {/*  */}
          <div
            style={{
              height: 10,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
              borderRadius: 8,
              overflow: 'hidden',
              marginBottom: 8,
            }}
          >
            <div
              style={{
                height: '100%',
                width: `${progress}%`,
                backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
              }}
            />
          </div>

          <Text style={{ fontSize: 14, marginBottom: 16, color: 'var(--tg-theme-text-color, #000)' }}>
            {raised.toLocaleString()} USDT ({progress}%)  {asset.goal.toLocaleString()} USDT
          </Text>

          {raised < asset.goal || SHOW_ADMIN ? (
            <Button size="l" style={{ width: '100%', marginBottom: 24 }} onClick={() => navigate(`/asset/${id}/buy`)}>
               
            </Button>
          ) : (
            <Button size="l" style={{ width: '100%', marginBottom: 24, backgroundColor: '#4CAF50', color: '#fff' }} onClick={handleClaim}>
               
            </Button>
          )}

          {/*   */}
          <ProfileRowContainer>
            <ProfileRow
              label=" :"
              value={asset.returnRate ?? ''}
              hint="     "
            />
            <ProfileRow
              label=" :"
              value={asset.lifetime ?? ''}
              hint="         "
            />
            <ProfileRow label=" :" value="1 USDT" />
            <ProfileRow label="  :" value={asset.saleStartDate ?? ''} last />
          </ProfileRowContainer>

          {/*   */}
          <Title level="3" style={{ marginBottom: 8, color: 'var(--tg-theme-text-color, #000)' }}>
             
          </Title>
          <div style={{ marginBottom: 24 }}>
            <Text style={{ color: 'var(--tg-theme-text-color, #000)', whiteSpace: 'pre-line' }}>
              {asset.description}
            </Text>
          </div>

          {/*  */}
          <div
            onClick={() =>
              window.Telegram?.WebApp?.openLink(`https://testnet.tonviewer.com/${id}?section=holders`)
            }
            style={{
              width: '94%',
              maxWidth: 570,
              marginBottom: 12,
              padding: '12px 16px',
              borderRadius: 8,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eaeaea)',
              color: 'var(--tg-theme-text-color, #000)',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              cursor: 'pointer',
              fontWeight: 600,
            }}
          >
            <Text style={{ fontSize: 18, fontWeight: 600 }}></Text>
            <Text style={{ fontSize: 20 }}></Text>
          </div>

          {/*  */}
          <div
            onClick={() => {
              if (asset.promoDoc) {
                window.Telegram?.WebApp?.openLink(asset.promoDoc);
              } else {
                alert('  ');
              }
            }}
            style={{
              width: '94%',
              maxWidth: 570,
              marginBottom: 12,
              padding: '12px 16px',
              borderRadius: 8,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eaeaea)',
              color: 'var(--tg-theme-text-color, #000)',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              cursor: 'pointer',
              fontWeight: 600,
            }}
          >
            <Text style={{ fontSize: 18, fontWeight: 600 }}></Text>
            <Text style={{ fontSize: 20 }}></Text>
          </div>

          {/*   */}
          {raised < asset.goal && (
            <Button size="l" style={{ width: '100%', marginBottom: 32 }} onClick={() => navigate(`/asset/${id}/buy`)}>
               
            </Button>
          )}
        </div>
      </div>
    </AppRoot>
  );
};
